body,html{
    padding:0;
    margin:0
}
.footer__social a,a{
    -webkit-transition:.2s ease;
    -o-transition:.2s ease;
    transition:.2s ease;
    text-decoration:none
}
.container,.footer,.header,.partners{
    overflow:hidden
}
.footer,.footer__copyright,.footer__social,.footer__social a i,.header,.how__item,.how__item__step,.partner{
    text-align:center
}
.footer__links a,.footer__social a,.nav__item a,a{
    text-decoration:none
}
.header__content h1,.header__content p,.nav__item a{
    text-shadow:0 0 1.5rem rgba(0,0,0,.4)
}
html{
    font-family:Muli,sans-serif;
    font-size:15px;
    font-size:calc(6px + 1vmin)
}
*,:after,:before{
    -webkit-box-sizing:border-box;
            box-sizing:border-box
}
h1,h2,h3,h4,h5,h6{
    font-family:Heebo,sans-serif;
    margin:0;
    color:#2a2a2a
}
h1{
    font-size:3.998em
}
h2{
    font-size:2.827em
}
h3{
    font-size:1.999em
}
h4{
    font-size:1.414em
}
p{
    line-height:1.5
}
a{
    color:#e7604a
}
a:active,a:focus,a:hover{
    color:#e44c33
}
section h2:first-child{
    text-align:center;
    text-transform:uppercase;
    letter-spacing:.1rem
}
.container{
    width:100%;
    max-width:1440px;
    margin:auto;
    padding:0 3rem
}
.footer{
    padding:3rem 0;
    background-color:#f7f7f7
}
.footer__copyright{
    color:#aaa;
    margin-bottom:.75rem
}
.footer__links a{
    display:inline-block;
    margin:.75rem 1.5rem;
    color:#e7604a
}
.footer__social{
    display:block;
    margin-top:.75rem
}
.footer__social a{
    margin:0 .75rem
}
.footer__social a i{
    display:inline-block;
    width:3rem;
    line-height:3rem;
    border-radius:4px;
    color:#fff
}
.footer__social .fa-facebook{
    background-color:#3b5998
}
.footer__social .fa-facebook:hover{
    background-color:#344e86
}
.footer__social .fa-twitter{
    background-color:#1da1f2
}
.footer__social .fa-twitter:hover{
    background-color:#0d95e8
}
.footer__social .fa-linkedin{
    background-color:#0077b5
}
.footer__social .fa-linkedin:hover{
    background-color:#00669c
}
.footer__social .fa-google-plus{
    background-color:#dd4b39
}
.footer__social .fa-google-plus:hover{
    background-color:#d73925
}
.header{
    position:relative;
    height:100vh;
    width:100%;
    background-image:url(../../Media/main_buyer.jpg);
    background-position:center;
    background-size:cover;
    color:#fff
}
.header:before{
    position:absolute;
    display:block;
    top:0;
    bottom:0;
    left:0;
    right:0;
    content:"";
    background:-webkit-gradient(linear,left top, left bottom,from(rgba(0,0,0,.1)),to(rgba(255,56,106,.0)));
    background:-webkit-linear-gradient(rgba(0,0,0,.1),rgba(255,56,106,.0));
    background:-o-linear-gradient(rgba(0,0,0,.1),rgba(255,56,106,.0));
    background:linear-gradient(rgba(0,0,0,.1),rgba(255,56,106,.0));
    z-index:1
}
.header__video{
    position:absolute;
    top:50%;
    left:50%;
    -webkit-transform:translate3d(-50%,-50%,0);
    transform:translate3d(-50%,-50%,0);
    min-width:100%;
    min-height:100%;
    height:auto;
    width:auto
}
.header__content{
    position:relative;
    top:50%;
    margin:auto;
    padding:0 3rem;
    -webkit-transform:translateY(-50%);
    -ms-transform:translateY(-50%);
    transform:translateY(-50%);
    color:#fff;
    z-index:1
}
@media screen and (min-width:1024px){
    .header__content{
        max-width:60%
    }
}
.header__content h1{
    color:#fff;
    letter-spacing:.2rem;
    margin:0 0 1rem;
    line-height:1.6;
    font-size:2rem
}
.header__content p{
    margin:0 0 4rem;
    font-weight:700;
    letter-spacing:.1rem
}
.nav__item a,.property__bid__status{
    letter-spacing:.4px;
    text-transform:uppercase;
    font-weight:700
}
.header__search{
    display:block;
    padding:0;
    margin:0;
    position:relative
}
.header__search input{
    opacity:.999;
    display:block;
    width:100%;
    padding:1.5rem 1.5rem 1.5rem 6rem;
    border:none;
    font-size:1.2rem;
    outline:0;
    border-radius:4px;
    -webkit-box-shadow:0 3px 12px 0 rgba(0,0,0,.12);
            box-shadow:0 3px 12px 0 rgba(0,0,0,.12)
}
.header__search input::-webkit-input-placeholder{
    -webkit-transition:all .2s;
    -o-transition:all .2s;
    transition:all .2s
}
.header__search input:-ms-input-placeholder{
    -webkit-transition:all .2s;
    -o-transition:all .2s;
    transition:all .2s
}
.header__search input::-ms-input-placeholder{
    -webkit-transition:all .2s;
    -o-transition:all .2s;
    transition:all .2s
}
.header__search input::placeholder{
    -webkit-transition:all .2s;
    -o-transition:all .2s;
    transition:all .2s
}
.header__search input:focus::-webkit-input-placeholder{
    opacity:.5
}
.header__search input:focus:-ms-input-placeholder{
    opacity:.5
}
.header__search input:focus::-ms-input-placeholder{
    opacity:.5
}
.header__search input:focus::placeholder{
    opacity:.5
}
.header__search button{
    display:block;
    border:none;
    outline:0;
    position:absolute;
    right:.5rem;
    top:50%;
    -webkit-transform:translateY(-50%);
    -ms-transform:translateY(-50%);
    transform:translateY(-50%);
    padding:1rem 3rem;
    font-size:1.2rem;
    background:#e7604a;
    color:#fff;
    cursor:pointer;
    border-radius:4px
}
.header__search img{
    display:inline-block;
    padding-right:1rem;
    border-right:1px solid rgba(0,0,0,.5);
    position:absolute;
    top:1rem;
    left:1rem;
    bottom:1rem;
    width:3.5rem;
    opacity:.5
}
.header__scroll-indicator:after,.header__scroll-indicator:before{
    height:15px;
    border-right:1px solid #fff;
    border-bottom:1px solid #fff;
    display:block;
    content:""
}
.header__scroll-indicator{
    display:block;
    position:absolute;
    bottom:3rem;
    left:50%;
    -webkit-transform:translateX(-50%);
    -ms-transform:translateX(-50%);
    transform:translateX(-50%);
    color:#fff;
    font-size:1.5rem;
    width:15px;
    height:15px;
    -webkit-animation:scroll;
    animation:scroll;
    -webkit-animation-duration:3s;
    animation-duration:3s;
    -webkit-animation-timing-function:ease-in-out;
    animation-timing-function:ease-in-out;
    -webkit-animation-iteration-count:infinite;
    animation-iteration-count:infinite;
    z-index:1
}
.header__scroll-indicator:before{
    -webkit-transform:rotate(45deg);
    -ms-transform:rotate(45deg);
    transform:rotate(45deg)
}
.header__scroll-indicator:after{
    -webkit-transform:rotate(45deg);
    -ms-transform:rotate(45deg);
    transform:rotate(45deg);
    margin-top:-8px
}
@-webkit-keyframes scroll{
    0%,100%{
        -webkit-transform:translateY(0);
        transform:translateY(0)
    }
    15%{
        -webkit-transform:translateY(10px);
        transform:translateY(10px)
    }
}
@keyframes scroll{
    0%,100%{
        -webkit-transform:translateY(0);
        transform:translateY(0)
    }
    15%{
        -webkit-transform:translateY(10px);
        transform:translateY(10px)
    }
}
.how{
    padding:3rem 0;
    margin:auto
}
.how__item{
    position:relative;
    margin-top:3rem
}
.nav,.nav__logo{
    position:absolute
}
@media screen and (min-width:600px) and (max-width:900px){
    .how__item{
        float:left;
        width:50%
    }
}
@media screen and (min-width:901px){
    .how__item{
        float:left;
        width:25%
    }
}
.how__item__step{
    display:block;
    border:2px solid #eee;
    border-radius:50%;
    width:80px;
    height:80px;
    line-height:76px;
    font-size:1.5rem;
    font-weight:700;
    background:#fff;
    color:#aaa;
    margin:0 auto 1.5rem
}
@media screen and (max-width:1100px){
    .how__item__step{
        display:none
    }
}
@media screen and (min-width:1101px){
    .how{
        padding:6rem 0
    }
    .how__item:not(:last-child) .how__item__step:after{
        content:"";
        display:block;
        position:absolute;
        top:39px;
        left:50%;
        width:100%;
        height:2px;
        background-color:#eee;
        z-index:-1
    }
}
.how__item img{
    margin-bottom:1rem;
    width:100px
}
.how__item h4{
    font-size:1.3em;
    padding:0 1rem;
    text-transform:uppercase
}
.how__item p{
    padding:0 1rem;
    color:#aaa
}
.nav{
    width:100%;
    padding:3rem;
    top:0;
    left:0;
    z-index:9
}
.nav--active .nav__toggle span:nth-child(1){
    -webkit-transform:translateY(8px) rotate(45deg)!important;
    -ms-transform:translateY(8px) rotate(45deg)!important;
    transform:translateY(8px) rotate(45deg)!important
}
.nav--active .nav__toggle span:nth-child(2){
    -webkit-transform:translate(-10px);
    -ms-transform:translate(-10px);
    transform:translate(-10px);
    opacity:0
}
.nav--active .nav__toggle span:nth-child(3){
    -webkit-transform:translateY(-8px) rotate(-45deg)!important;
    -ms-transform:translateY(-8px) rotate(-45deg)!important;
    transform:translateY(-8px) rotate(-45deg)!important
}
.nav__logo{
    top:50%;
    -webkit-transform:translateY(-65%);
    -ms-transform:translateY(-65%);
    transform:translateY(-65%);
    z-index:1
}
.nav__logo img{
    max-height:4rem
}
.nav__items{
    float:right;
    margin:0;
    padding:0
}
.nav__item{
    display:inline-block;
    margin-left:1.5rem
}
.nav__item a{
    display:block;
    padding-bottom:.5rem;
    color:#fff;
    cursor:pointer;
    border-bottom:1px solid transparent;
    -webkit-transition:.2s ease;
    -o-transition:.2s ease;
    transition:.2s ease;
    -ms-touch-action:manipulation;
    touch-action:manipulation
}
.nav__item a:hover{
    border-bottom-color:rgba(255,255,255,.3)
}
.nav__item--important a{
    padding:.5rem 1.5rem;
    background:#e7604a;
    border-radius:4px
}
.nav__item--important a:hover{
    border-bottom-color:transparent
}
.nav__toggle{
    position:relative;
    display:none;
    float:right;
    padding:0;
    background:0 0;
    border:none;
    width:2rem;
    outline:0;
    cursor:pointer;
    z-index:1;
    -ms-touch-action:manipulation;
    touch-action:manipulation
}
.nav__toggle span{
    display:block;
    margin:6px 0;
    height:2px;
    width:100%;
    background:#fff;
    -webkit-transition:.2s ease;
    -o-transition:.2s ease;
    transition:.2s ease
}
.nav__toggle:hover span:nth-child(1),.nav__toggle:hover span:nth-child(3){
    -webkit-transform:translateX(5px);
    -ms-transform:translateX(5px);
    transform:translateX(5px)
}
@media screen and (max-width:1000px){
    .nav--active .nav__items{
        -webkit-transform:scale(1);
        -ms-transform:scale(1);
        transform:scale(1);
        opacity:1
    }
    .nav__toggle{
        display:inline-block
    }
    .nav__items{
        position:absolute;
        float:none;
        padding-top:6rem;
        top:0;
        left:0;
        width:100%;
        text-align:center;
        background:#282f39;
        -webkit-transform:scale(0);
        -ms-transform:scale(0);
        transform:scale(0);
        opacity:0;
        -webkit-transition:opacity .2s ease;
        -o-transition:opacity .2s ease;
        transition:opacity .2s ease;
        -webkit-box-shadow:0 3px 12px 0 rgba(0,0,0,.12);
                box-shadow:0 3px 12px 0 rgba(0,0,0,.12)
    }
    .nav__item{
        display:block;
        margin:0;
        padding:1rem 0;
        border-bottom:1px solid rgba(255,255,255,.1)
    }
    .nav__item--important{
        padding:0;
        border:0
    }
    .nav__item--important a{
        padding:1rem 0;
        border-radius:0
    }
}
.partners h2,.property{
    margin-bottom:3rem
}
.partners{
    padding:3rem 0
}
@media screen and (min-width:1101px){
    .partners{
        padding:6rem 0
    }
