.properties .container{
    padding:3rem;
    border-bottom:1px solid #eee
}
.property{
    display:inline-block;
    float:left;
    -webkit-transition:all .4s;
    -o-transition:all .4s;
    transition:all .4s;
    border-radius:4px;
    cursor:default;
    border:1px solid #eee;
    -webkit-box-shadow:0 0 6px -2px #b4b6c1;
            box-shadow:0 0 6px -2px #b4b6c1
}
@media screen and (min-width:1101px){
    .properties .container{
        padding:6rem 3rem
    }
    .property{
        width:30%;
        width:calc((100% - 6rem)/ 3)
    }
    .property:nth-child(3n+2){
        margin-left:3rem;
        margin-right:3rem
    }
}
@media screen and (min-width:611px) and (max-width:1100px){
    .property{
        width:45%;
        width:calc((100% - 3rem)/ 2)
    }
    .property:nth-child(odd){
        margin-right:3rem
    }
}
@media screen and (max-width:610px){
    .property{
        display:block;
        float:none
    }
}
.property:hover{
    -webkit-box-shadow:0 0 30px -5px #b4b6c1;
            box-shadow:0 0 30px -5px #b4b6c1
}
.property__bid{
    position:relative;
    color:#fff;
    text-shadow:0 0 5px rgba(0,0,0,.4)
}
.property__bid__image{
    padding-bottom:65%;
    background-position:center;
    background-size:cover;
    border-radius:4px 4px 0 0
}
.property__bid__image:after,.property__bid__image:before{
    position:absolute;
    content:"";
    display:block;
    width:100%;
    left:0
}
.property__bid__image:before{
    height:20%;
    top:0;
    background:-webkit-gradient(linear,left top, left bottom,from(rgba(0,0,0,.3)),to(transparent));
    background:-webkit-linear-gradient(rgba(0,0,0,.3),transparent);
    background:-o-linear-gradient(rgba(0,0,0,.3),transparent);
    background:linear-gradient(rgba(0,0,0,.3),transparent)
}
.property__bid__image:after{
    height:35%;
    top:65%;
    background:-webkit-gradient(linear,left top, left bottom,from(transparent),to(rgba(0,0,0,.3)));
    background:-webkit-linear-gradient(transparent,rgba(0,0,0,.3));
    background:-o-linear-gradient(transparent,rgba(0,0,0,.3));
    background:linear-gradient(transparent,rgba(0,0,0,.3))
}
.property__bid__status{
    position:absolute;
    top:1rem;
    right:1rem
}
.property__bid__status i{
    font-size:1.8rem;
    vertical-align:bottom
}
.property__bid__price,.property__bid__time{
    position:absolute;
    display:inline-block;
    bottom:1rem;
    z-index:1
}
.property__bid__price small,.property__bid__time small{
    display:block;
    text-transform:uppercase;
    letter-spacing:.7px;
    font-size:.65rem
}
.property__bid__time{
    left:1rem;
    text-align:left
}
.property__bid__time time{
    line-height:1.6rem
}
.property__bid__price{
    right:1rem;
    text-align:right
}
.property__bid__price span{
    font-size:1.2rem;
    line-height:1.6rem
}
.property__info{
    padding:1rem
}
.property__info__title{
    font-size:1.6rem;
    line-height:1.4;
    margin:0 0 .4rem
}
.property__info__location{
    margin:0 0 1rem;
    padding-bottom:1rem;
    border-bottom:1px solid #eee
}
.property__info__offered-by{
    font-size:.8rem
}
.property__info__offered-by span{
    font-weight:700
}
.property__info__agent{
    position:relative;
    padding-left:3.5rem;
    min-height:2.5rem
}
.property__info__agent__image{
    position:absolute;
    left:0;
    top:0;
    width:2.5rem;
    height:2.5rem;
    border-radius:100%;
    -webkit-box-shadow:0 0 6px -2px #b4b6c1;
            box-shadow:0 0 6px -2px #b4b6c1
}
.property__info__agent__name{
    display:block;
    font-size:.85rem;
    letter-spacing:.35px
}
.property__info__agent__rating{
    line-height:1.5;
    color:#e7604a
}
.properties__view-more{
    display:block;
    margin:auto;
    border:none;
    padding:1rem 3rem;
    font-size:1.4rem;
    background:#e7604a;
    color:#fff;
    cursor:pointer;
    border-radius:4px
}
.testimonials{
    padding:3rem 0;
    position:relative;
    background-color:#f7f7f7
}
@media screen and (min-width:1101px){
    .testimonials{
        padding:6rem 0
    }
}
.testimonials__arrows{
    text-align:center
}
.testimonials__arrow__left{
    cursor:pointer;
    color:#aaa;
    font-size:2.5rem;
    margin-right:1rem;
    margin-left:1rem
}
.testimonials__slider{
    margin:0 -1.5rem
}
.testimonial{
    width:100%/3;
    margin-top:1.5rem;
    padding:0 1.5rem
}
.testimonial__text{
    padding:1rem;
    background:#fff;
    margin-bottom:1.5rem;
    position:relative;
    border-radius:4px;
    -webkit-box-shadow:0 1px 15px -8px #000;
            box-shadow:0 1px 15px -8px #000
}
.testimonial__text:after{
    content:"";
    display:block;
    position:absolute;
    bottom:-8px;
    -webkit-transform:rotate(45deg);
    -ms-transform:rotate(45deg);
    transform:rotate(45deg);
    left:3rem;
    width:16px;
    height:16px;
    background:#fff;
    -webkit-box-shadow:2px 2px 10px -5px #000;
            box-shadow:2px 2px 10px -5px #000
}
.testimonial__text p{
    margin:0;
    color:#aaa
}
.testimonial__user{
    position:relative;
    padding-left:7.5rem;
    min-height:4.5rem
}
.testimonial__user img{
    position:absolute;
    width:4.5rem;
    height:4.5rem;
    left:1.5rem;
    border-radius:50%;
    background-position:center;
    background-size:cover;
    display:inline-block;
    -webkit-filter:grayscale(100);
    filter:grayscale(100);
    vertical-align:middle
}
.testimonial__user h4{
    font-size:1.2em;
    margin:0;
